.quote-container {
    text-align: center;
    padding: 10px;
    background-color: #e5f0e63f;
    /* border: 1px solid #033a35; */
    border-radius: 8px;
    width: 60vw;
}

.quote {
    font-size: 18px;
    font-style: italic;
    color: #033a35;
}

.author {
    font-size: 12px;
    display: block;
    margin-top: 4px;
    font-weight: bold;
    color: #033a35;
}
